body{
    /* background-image: url('../assests/bg.jfif'); */
    /* background: url('../assests/bgvideo.mp4'); */
    overflow: hidden;
}

*{
    margin: 0;
    padding:0;

}


.container{
   
    /* position: absolute; */
    padding-top: 30px;
    width:607px;
    height:829px;
    margin:auto;
    margin-top: 100px;
    border-radius: 100px;
    background: #000;
    opacity: .7;
    backdrop-filter:blur(400px);
    box-shadow: 0 0 40px rgba(8,7,6,0,6);
    /* background:linear-gradient(180deg,#130754 0%,#4d2f80 100%); */
    z-index: 5;
}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 16px;
}
.top-bar input{
    display: flex;
    width: 362px;
    height: 78px;
    background: #f7fdff;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #3b3a3c;
    font-size: 30px;
    font-weight: bold;
}

.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 78px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}

.weather-image, .data-container, .weather-location, .weather-temp, .element{
    display: flex;
    justify-content: center;
    color:#ffff;
}

.weather-image{
    margin-top: 29px;
}

.weather-temp{
    font-size:120px ;
    font-weight:400;
}

.weather-location{
    font-size:60px;
    font-weight:400;
}

.data-container{
    margin-top: 50px;
}

.element{
    margin:auto;
    align-items: flex-start;
    gap:12px;
}

.data{
    font-size:34px;
    font-weight:400;
}

.text{
    font-size: 20px;
    font-weight: 400;
}

.icon{
    margin-top: 10px;
}


.app video{
    position: absolute;
    z-index: -1;
    top: 0;
    left:0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
}
